.logo{
	height: 60px;  
	 max-width: 15.8% !important;
	 background: #6849D7 !important; 
  }
  .logo img{
	 height:82px;
	 margin-top: -11px;
	 object-fit: contain;
	 width:100%;
	 margin-left:10px
  }
  .admin-right{
	  color: #8B8B8B;
	  
  }
  .profile-name{
	  text-align: right;
	 padding: 19px 15px;
  }
  .profile-img{
	  padding-right: 25px;
  }
  .profile-img img{
	  margin-top: 5px;
	 border-radius: 100%;
	 box-sizing: border-box;	height: 49px;	width: 49px;	border: 2px solid #FFFFFF;
  }
  .m-r-25{
	 margin-right: 25px;
 }
 @media(min-width:993px) and (max-width:1199px){
	 .logo img{
		 width:170px;
	 }
	 .logo{
		 max-width: 16.4% !important
	 }
 
 }
 @media (min-width:768px) and (max-width:992px){
	 .logo{
		 max-width: 21.7% !important
	 }
 }
 @media (min-width:100px) and (max-width:599px){
	 .logo{
		 max-width: 33.3% !important
	 }
 }
 @media (min-width:1899px) and (max-width:4999px){
	 .logo{
		 max-width: 15.3% !important
	 }
 }
 