.carousel .slide{

  background: transparent;
}
.carousel .slider-wrapper{margin-left:265px}


.alice-carousel__prev-btn,
.alice-carousel__next-btn {
 width: auto;
 position: absolute;
 top:50px;
}


.alice-carousel__prev-btn{
 left:0;
margin-left: -24px
}


.alice-carousel__prev-btn [data-area]::after {
 font-family: "FontAwesome"; 
 content: "\f0d9";
 font-size: 50px;
 color:#dcdcdc;

 
}
@media (min-width:100px) and (max-width:767px){
  .alice-carousel__prev-btn [data-area]::after {display: none}

  .alice-carousel__next-btn [data-area]::after{display: none}
}
@media (min-width:768px) and (max-width:992px){
  .alice-carousel__next-btn [data-area]::after{ margin-right: 34px}
}

.alice-carousel__next-btn [data-area]::after {
  font-family: "FontAwesome"; 
  content: "\f0da";
  font-size: 50px;
  color: #dcdcdc;
 }

 .alice-carousel__prev-btn [data-area]:hover::after, .alice-carousel__next-btn [data-area]:hover::after {
   opacity: 1;
   color: #6849d7
 }

.alice-carousel__next-btn{
 right:0;
 margin-right:-17px
}
@media (min-width:768px) and (max-width:992px){
  .alice-carousel__prev-btn,
.alice-carousel__next-btn{
top:20px !important
}
}
