.react-datepicker__input-container input{border: 0px !important;color: #68808D;width:145px;height: 36px;border: 1px solid #E3E6E9;border-radius: 3px;font-size: 14px;font-weight: 500;line-height: 19px;margin-left:27px;}
.react-datepicker__input-container::before{
	content:url('../../../images/daterange.png');
	position: absolute;
	z-index: 999;margin-top:8px;
}
.react-datepicker__input-container::after{
	content:url('../../../images/arrow-down.png');

	position: absolute;
	z-index: 999;margin-top:8px;margin-left:-30px
}

.react-datepicker__input-container{border:1px solid #dcdcdc;font-size:13px;padding:1px 10px;width:209px;background:"#fff"}
.react-datepicker__input-container:focus{border:0px !important; outline: none !important;

}
.react-datepicker-wrapper{background:#fff;}