ul li:hover{
background: transparent !important
}.customStarBlank{
    color:#E6E6E6;
    font-size:20px;
    margin-right:10px;
    margin-top:-2px;
    right:0;
    position: absolute;
}
.customStarFilled{
    color:#6849D7;
    font-size:20px;
    right:0;
    position: absolute;
    margin-top:-2px;
    margin-right:10px;
}
.carousel-control-prev{
    background: #dcdcdc;
    opacity: 1;
    margin-left: -40px;
    border-radius: 50%;
    height: 40px;
    width: 40px;
}
.carousel-control-next{
    background: #dcdcdc;
    opacity: 1;
    margin-right: -50px;
    border-radius: 50%;
    height: 40px;
    width: 40px;
}
.carousel-indicators li{
    height:auto !important
}