#relatedHeading:after {
    content: "";
    display: block;
    width: 35%;
    padding-top: 15px;
    border-bottom: 2px solid #6849D7;
    margin-left: 4px;
}



#customBoxDiv:hover{
	border-bottom: 2px solid #6849D7;
}
@media (min-width:100px) and (max-width:767px){
    #relatedHeading:after {
        content: "";
        display: none;
      
    }
}